body {
    background-color: #313131;
    color: #F5F5F5;
    font-family: 'VT323', monospace;
}

h1 {
    font-weight: lighter;
    font-size: 40px;
}

h5 {
    font-weight: lighter;
    font-stretch: extra-expanded;
}

.title {
    background-color: #212121;
    width: 100%;
    padding-left: 20px;
}

.pixel {
    background-color: #414141;
    height: 16px;
    width: 16px;
    border: 1px solid #525252
}

.pixelNoGrid {
    background-color: #414141;
    height: 20px;
    width: 20px;
    border: none;
}

.input {
    background-color: #525252;
    margin: auto;
    margin: 5px;
    color: #F5F5F5
}

.button {
    border: none;
    border-radius: 1px;
    height: 30px;
    font-family: 'VT323', monospace;
}

.color {
    height: 38px;
    width: 38px;
    border: 2px; 
    border-color: #525252;
    border-style: outset;
}

.noGrid * {
    border: none;
    border-collapse: collapse;
    padding: 0;
}

.noGrid {
    border: none;
    border-collapse: collapse;
}

.grid {
    border: none;
    border-collapse: collapse;
}

.gridArea {
    background-color: #212121;
    width: 640px;
    height: 640px;
    padding: 20px;
    float: left;
}

.settings {
    padding-left: 5px;
}

.container {
    margin: auto;
}

.Palette {
    clear: both;
    margin: auto;
}

.colors {
    float: left;
    margin: auto;
}

.App {
    height: 100%;
    width: 100%;
}